.assessment-header {
    text-align: center;
    margin-bottom: 30px;
}

.assessment-subtitle {
    color: #e0e0e0;
    font-size: 16px;
    line-height: 1.6;
    max-width: 900px;
    margin: 15px auto 0;
}

.compliance-warning {
    color: #ff9800;
    font-weight: 600;
}

.progress-section {
    background: rgba(255, 255, 255, 0.05);
    border-radius: 15px;
    padding: 25px;
    margin-bottom: 30px;
    border: 1px solid rgba(255, 255, 255, 0.1);
}

.progress-stats {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 40px;
    margin-bottom: 20px;
}

.stat-item {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.stat-number {
    font-size: 36px;
    font-weight: bold;
    color: #21b0db;
}

.stat-label {
    font-size: 14px;
    color: #b0b0b0;
    margin-top: 5px;
}

.stat-divider {
    width: 2px;
    height: 50px;
    background: rgba(255, 255, 255, 0.2);
}

.progress-bar-wrapper {
    height: 40px;
    background: rgba(0, 0, 0, 0.3);
    border-radius: 20px;
    overflow: hidden;
    position: relative;
}

.progress-bar-fill {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.5s ease;
    border-radius: 20px;
}

.progress-label {
    color: white;
    font-weight: 600;
    font-size: 14px;
    text-shadow: 0 1px 3px rgba(0, 0, 0, 0.3);
}

.measures-grid {
    display: grid;
    grid-template-columns: 1fr;
    gap: 15px;
    margin-bottom: 30px;
}

.measure-card {
    background: rgba(255, 255, 255, 0.05);
    border: 2px solid rgba(255, 255, 255, 0.1);
    border-radius: 12px;
    padding: 20px;
    display: flex;
    gap: 20px;
    cursor: pointer;
    transition: all 0.3s ease;
}

.measure-card:hover {
    background: rgba(255, 255, 255, 0.08);
    border-color: #21b0db;
    transform: translateY(-2px);
    box-shadow: 0 5px 20px rgba(33, 176, 219, 0.2);
}

.measure-card.checked {
    background: rgba(76, 175, 80, 0.1);
    border-color: #4caf50;
}

.measure-card.priority-high {
    border-left: 4px solid #f44336;
}

.measure-card.priority-medium {
    border-left: 4px solid #ff9800;
}

.measure-checkbox-wrapper {
    flex-shrink: 0;
}

.measure-checkbox-input {
    display: none;
}

.measure-checkmark {
    width: 28px;
    height: 28px;
    border: 2px solid rgba(255, 255, 255, 0.3);
    border-radius: 6px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: rgba(0, 0, 0, 0.2);
    transition: all 0.3s ease;
}

.measure-card.checked .measure-checkmark {
    background: #4caf50;
    border-color: #4caf50;
}

.checkmark-icon {
    color: white;
    font-size: 18px;
    font-weight: bold;
}

.measure-content {
    flex: 1;
}

.measure-header-row {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    gap: 15px;
    margin-bottom: 10px;
}

.measure-title {
    font-size: 18px;
    font-weight: 600;
    color: white;
    margin: 0;
    flex: 1;
}

.priority-badge {
    padding: 4px 12px;
    border-radius: 20px;
    font-size: 12px;
    font-weight: 600;
    white-space: nowrap;
}

.priority-badge.high {
    background: rgba(244, 67, 54, 0.2);
    color: #ff6b6b;
}

.priority-badge.medium {
    background: rgba(255, 152, 0, 0.2);
    color: #ffa726;
}

.measure-description {
    color: #d0d0d0;
    font-size: 14px;
    line-height: 1.6;
    margin: 0 0 10px 0;
}

.measure-article {
    display: inline-block;
    font-size: 12px;
    color: #21b0db;
    background: rgba(33, 176, 219, 0.1);
    padding: 3px 10px;
    border-radius: 12px;
    font-weight: 500;
}

.assessment-footer {
    background: rgba(33, 176, 219, 0.1);
    border-left: 4px solid #21b0db;
    border-radius: 8px;
    padding: 20px;
    margin-bottom: 30px;
}

.footer-note {
    color: #e0e0e0;
    font-size: 14px;
    line-height: 1.6;
    margin: 0;
}

.form-actions {
    display: flex;
    justify-content: space-between;
    gap: 20px;
}

.back-btn,
.continue-btn {
    padding: 15px 40px;
    border-radius: 8px;
    border: none;
    font-size: 16px;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.3s ease;
}

.back-btn {
    background: rgba(255, 255, 255, 0.1);
    color: white;
}

.back-btn:hover {
    background: rgba(255, 255, 255, 0.15);
}

.continue-btn {
    background: linear-gradient(135deg, #21b0db 0%, #1f4565 100%);
    color: white;
    flex: 1;
}

.continue-btn:hover {
    transform: translateY(-2px);
    box-shadow: 0 5px 20px rgba(33, 176, 219, 0.4);
}

@media (max-width: 768px) {
    .progress-stats {
        flex-direction: column;
        gap: 20px;
    }

    .stat-divider {
        width: 100px;
        height: 2px;
    }

    .measure-card {
        flex-direction: column;
    }

    .form-actions {
        flex-direction: column;
    }
}