/* SummaryPage.css - Unified Apple Dark Mode */

.summary-box {
    border-radius: var(--radius-card);
    padding: 24px;
    margin-bottom: 24px;
    border: 1px solid var(--border-subtle);
    background: rgba(255, 255, 255, 0.02);
}

.summary-box h3 {
    font-size: 1.25rem;
    margin-bottom: 8px;
    color: var(--text-main);
}

.summary-box p {
    font-size: 1rem;
    line-height: 1.6;
    color: var(--text-dim);
}

/* Status Colors (Subtle Text/Border, not full backgrounds) */
.summary-none {
    border-left: 4px solid var(--accent-green);
}

.summary-lower {
    border-left: 4px solid #ff9f0a;
}

.summary-higher {
    border-left: 4px solid var(--accent-red);
}

.reasoning-box {
    margin-top: 16px;
    padding: 16px;
    background: rgba(255, 255, 255, 0.05);
    /* Slight lift */
    border-radius: 8px;
}

.reasoning-box h4 {
    color: var(--text-main);
    margin-bottom: 4px;
}

.reasoning-box p {
    color: var(--text-dim);
    font-style: italic;
}

.info-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 16px;
    margin-bottom: 32px;
}

.info-item {
    background: var(--bg-card-hover);
    padding: 16px;
    border-radius: 12px;
    border: 1px solid var(--border-subtle);
}

.info-label {
    display: block;
    color: var(--text-dim);
    font-size: 0.85rem;
    margin-bottom: 4px;
}

.info-value {
    display: block;
    color: var(--accent-purple);
    font-size: 1.1rem;
    font-weight: 600;
}

.obligations-list li {
    padding: 12px 12px 12px 30px;
    margin-bottom: 8px;
    background: transparent;
    border-bottom: 1px solid var(--border-subtle);
    color: var(--text-main);
    position: relative;
}

.obligations-list li:before {
    content: "✓";
    position: absolute;
    left: 0;
    color: var(--accent-green);
    font-weight: bold;
}