/* SectorSelection.css - Unified Styling */

.sector-description {
    color: var(--text-dim);
    margin-bottom: 24px;
    font-size: 1rem;
    line-height: 1.5;
}

.sector-select {
    /* Inherits basic input styles from App.css */
    height: 50px;
    /* Taller touch target */
}

optgroup {
    background: var(--bg-card);
    color: var(--text-main);
    font-weight: bold;
}

.sector-info {
    margin-top: 32px;
    padding: 24px;
    border-radius: var(--radius-card);
    background: rgba(255, 255, 255, 0.03);
    border: 1px solid var(--border-subtle);
}

.sector-info h3 {
    margin-bottom: 12px;
    font-size: 1.1rem;
}

.form-actions {
    display: flex;
    justify-content: space-between;
    gap: 16px;
    margin-top: 40px;
}

.back-btn {
    /* Inherits .btn-secondary from App.css map */
    background-color: #3a3a3c;
    color: var(--text-main);
}